import { Component, OnInit, Inject } from '@angular/core';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';


@Component({
  selector: 'app-alert-dialog',
  templateUrl: './alert-dialog.component.html',
  styleUrls: ['./alert-dialog.component.css'],
})
export class AlertDialogComponent implements OnInit {
  public title: string;
  public message: string;
  public dontshow: string;
  public subkey: string;
  public imagePath: string;
  public imageAlt: string;
  dontshowanymore: boolean;

  constructor(
    @Inject(MAT_DIALOG_DATA)
    private data: {
      Message: string;
      Title: string;
      DontShow: string;
      SubKey: string;
      ImagePath: string;
      ImageAlt: string;

    },
    private mdDialogRef: MatDialogRef<AlertDialogComponent>
  ) {
    this.title = '';
    this.message = '';
    this.dontshow = '';
    this.subkey = '';
    this.imagePath = '';
    this.imageAlt = '';
    this.dontshowanymore = false;
  }

  ngOnInit() {
    this.title = this.data.Title;
    this.message = this.data.Message;
    this.dontshow = this.data.DontShow;
    this.subkey = this.data.SubKey;
    this.imagePath = this.data.ImagePath;
    this.imageAlt = this.data.ImageAlt;
  }


showOptions(event:MatCheckboxChange): void {

    if (this.dontshowanymore)
      sessionStorage.setItem('DONTSHOWUPDATEWARN' , this.message);
      else
      sessionStorage.removeItem('DONTSHOWUPDATEWARN' );
}
  public closeDialog() {
    this.mdDialogRef.close();

  }
}
