import { Injectable, Inject, Injector, EventEmitter } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Router, RouterStateSnapshot } from '@angular/router';

import * as _ from 'lodash';

import { of, Subject } from 'rxjs';
import { tap, map, catchError, timeout } from 'rxjs/operators';

import { StorageVars } from './models/storage-vars';

import { LoginRequest, ChangePasswordInfo } from './models/login-request';
import { LoginResponse } from './models/login-response';
import { LogoffRequest } from './models/logoff-request';
import { LogoffResponse } from './models/logoff-response';
import { IsValidTokenRequest } from './models/is-valid-token-request';
import { OperationResult } from './models/operation-result';
import { TbAuthEnvironment } from './models/auth-environment';
import { ChangePasswordDialogComponent } from './pages/change-password-dialog/change-password-dialog.component';
import { AlertDialogComponent } from './pages/alert-dialog/alert-dialog.component';
import { MatDialog } from '@angular/material/dialog';

let authServiceInstance: TbAuthService;
export const authService = () => authServiceInstance;

@Injectable({
    providedIn: 'root',
})
export class TbAuthService {
    private static DEFAULT_ENV: TbAuthEnvironment = {
        auth: {
            url: 'http://localhost:10344/api/',
            iupurl: 'http://localhost:52172/api/',
            createAccountUrl: 'http://localhost:4200',
            changePasswordUrl: 'http://localhost:56392/api/',
            subscriptionSelection: false,
            showSignUp: false,
            appId: 'M4',
            redirectUrl: '/',
            userGatewayUrl: '',
            isRedirectExternal: true,
            loginPageUrl: 'login',
            sessionStorage: false,
            snapshotServiceUrl: '',
            updatemessageIt: 'La tua subscription @@sub sta per essere aggiornata, non sarà quindi utilizzabile nella data prevista: @@date',
            updatemessageEn: 'Your subscription @@sub is going to be updated, you are not going to be able to use it on: @@date',
            logoURL: 'https://magocloud-store-pdf.s3.eu-west-1.amazonaws.com/login-logo.png',
            // tslint:disable-next-line: max-line-length
            //'data:image/png;base64,'
        },
    };
    private env: TbAuthEnvironment;

    loggedOut$ = new Subject();
    errorMessage = '';
    okMessage = '';

    get router() {
        return this.injector.get<Router>(Router);
    }

    constructor(@Inject('env') env: TbAuthEnvironment, private http: HttpClient, private injector: Injector, private dialog: MatDialog) {
        authServiceInstance = this;
        this.env = _.defaultsDeep(env, TbAuthService.DEFAULT_ENV, env);
        console.log('TbAuthEnvironment', this.env);
    }

    async checkConnection() {
        return await this.http
            .get(this.getBaseUrl())
            .pipe(
                timeout(5000),
                map((__) => true)
            )
            .toPromise()
            .catch((__) => false);
    }

    /**
     * Ritorna la base url del backend,
     * caricata da un file di configurazione caricato dinamicamente (assets/environment.json)
     */
    getBaseUrl = (): string => this.env.auth.url;
    getSnapshotServiceUrl = (): string => this.env.auth.snapshotServiceUrl;
    getLoginPageUrl = (): string => this.env.auth.loginPageUrl;

    /*
  {
    type: JWT,
    appid: M4,
    securityValue: jwtEncoded
  }
*/
    getAuthorizationHeader(): string {
        return JSON.stringify({
            Type: 'JWT',
            AppId: 'M4',
            SecurityValue: this.getToken(),
        });
    }

    prelogin(loginRequest: LoginRequest): Promise<LoginResponse> {
        // console.log('authService.login - loginRequest', loginRequest);

        return this.http
            .post<LoginResponse>(this.getPreLoginUrl(), loginRequest)
            .pipe(
                map((loginResponse: LoginResponse) => {
                    if (!loginResponse.Result) {
                        if (loginResponse.ResultCode === 19) {
                            // mi sposto su pagina per cambio password e nuovo tentativo di login
                            console.log('AuthService: Change Password Needed');
                            this.openChangePasswordDialog(loginRequest);
                        } else if (loginResponse.ResultCode === 4) {
                            console.log('AuthService: Account confirmation Needed');
                            // mi sposto su pagina per attivare l'account che non ha ancora effettuato la procedura?
                        }
                        loginResponse.Message = loginResponse.Message ? loginResponse.Message : 'Login error...';
                        this.errorMessage = loginResponse.Message;
                        return loginResponse;
                    }
                    return loginResponse;
                })
            )
            .toPromise();
    }

    login(loginRequest: LoginRequest): Promise<LoginResponse> {
        // console.log('authService.login - loginRequest', loginRequest);
        return this.http
            .post<LoginResponse>(this.getLoginUrl(), loginRequest)
            .pipe(
                map((loginResponse: LoginResponse) => {
                    if (!loginResponse.Result) {
                        if (loginResponse.ResultCode === 19) {
                            // mi sposto su pagina per cambio password e nuovo tentativo di login
                            console.log('AuthService: Change Password Needed');
                            this.openChangePasswordDialog(loginRequest);
                        } else if (loginResponse.ResultCode === 4) {
                            console.log('AuthService: Account confirmation Needed');
                            // mi sposto su pagina per attivare l'account che non ha ancora effettuato la procedura?
                        } else {
                            this.clearStorage();
                            console.log('AuthService: Clearing storage due to Login failure, result code ', loginResponse.ResultCode);
                            console.log('LoginRequest by account' + loginRequest.accountName + ' token:' + loginRequest.token);
                        }
                        loginResponse.Message = loginResponse.Message ? loginResponse.Message : 'Login error...';
                        console.log(loginResponse.Message);
                        this.errorMessage = loginResponse.Message;
                        return loginResponse;
                    }

                    this.storageData(loginResponse);

                    return loginResponse;
                })
            )
            .toPromise();
    }

     // ---------------------------------------------------------------------------
    async openAlertDialog(info: string, title: string, dontshow: string, accountName: string, subscriptionKey: string) {
        this.errorMessage = '';

        const dialogRef = this.dialog.open(AlertDialogComponent, {
            data: {
                Title:  title,
                Message: info,
                DontShow: dontshow,
                SubKey: subscriptionKey
            },
        });
        dialogRef.afterClosed().subscribe(() => {
          console.log('afterClosedAlert');
        this.okMessage = '';
        this.errorMessage = '';

        if (this.isRedirectExternal()) {
          console.log('go external.');
          this.getRedirectUrlForSubscription(accountName, subscriptionKey);
          return;
        }

        console.log('go internal!');
        this.router.navigate([this.getRedirectUrl()]);

        });

    }

    // ---------------------------------------------------------------------------
    async openChangePasswordDialog(loginRequest: LoginRequest) {
        this.errorMessage = '';

        const dialogRef = this.dialog.open(ChangePasswordDialogComponent, {
            data: {
                Title: 'Change Password',
                Message: 'Please choose a new password: ',
                PlaceHolder: 'Password',
                PlaceHolder2: 'Confirm Password',
                NewPwd: '',
            },
        });

        dialogRef.afterClosed().subscribe(async (data: { NewPwd: string | undefined }) => {
            if (data === undefined) return;
            if (data.NewPwd === undefined || data.NewPwd === '') {
                alert('Write a valid AccountName');
                return;
            }
            this.errorMessage = '';
            const cpi: ChangePasswordInfo = new ChangePasswordInfo();
            cpi.AccountName = loginRequest.accountName;
            cpi.IgnoreOldPassword = false;
            cpi.JWTToken = loginRequest.token;
            cpi.NewPassword = data.NewPwd;
            cpi.Password = loginRequest.password;

            loginRequest.password = data.NewPwd;
            const result = await this.changePassword(cpi).catch((err) => {
                this.errorMessage = err.error && err.error.Message;
                return;
            });

            // todo controlla come vengono mostrati errori
            if (result && result.Result) {
                this.errorMessage = '';
                this.okMessage = 'Password changed succesfully!';
                //la login la fa  a mano altrimenti mi perdo
            } else {
                //errore già indicato
                loginRequest.token = '';
                loginRequest.password = '';
                loginRequest.subscriptionKey = '';
                loginRequest.appId = '';
            }
        });
    }

    async isValidToken(authtoken = ''): Promise<OperationResult> {
        if (!authtoken) {
            const opres = new OperationResult();
            opres.Message = 'No authtoken';
            return opres;
        }

        return this.http
            .post<OperationResult>(this.getIsValidTokenUrl(), new IsValidTokenRequest(authtoken))
            .pipe(
                tap((jObj: OperationResult) => {
                    // console.log('isValidToken - response', jObj);
                    if (!jObj.Result) {
                        jObj.Message = jObj.Message ? jObj.Message : 'isValidToken error...';
                        console.log('AuthService: Clearing storage due to Token Validation failure');
                        console.log('token: ' + authtoken);
                        this.clearStorage();
                        this.errorMessage = jObj.Message;
                    }
                }),
                catchError((error: HttpErrorResponse) => {
                    console.error(`Error Code: ${error.status}\nMessage: ${error.message}`);
                    const res = new OperationResult();
                    res.Code = 666;

                    if (!this.router.routerState.snapshot.url.includes(this.getLoginPageUrl()))
                        this.router.navigate([this.getLoginPageUrl()]);
                    return of(res);
                })
            )
            .toPromise();
    }

    public getCompaniesForUser(user: string) {
        return this.http.get<OperationResult>(this.getSubsKeysForAccountUrl() + user).pipe(
            map((res: any) => {
                if (!res || !res.Result) return [];
                return res.Content && res.Content.subscriptions ? res.Content.subscriptions : [];
            })
        );
    }

    public getIsValidTokenUrl() {
        return this.getBaseUrl() + 'isvalidtoken/';
    }

    public getLoginUrl() {
        return this.getBaseUrl() + 'login/';
    }
    getPreLoginUrl() {
        return this.getBaseUrl() + 'login/';
    }

    public getLogoutUrl() {
        return this.getBaseUrl() + 'logoff/';
    }

    public getChangePasswordApiUrl() {
        return this.getChangePasswordUrl() + 'changepassword/';
    }

    public getResetPasswordUrl() {
        return this.getChangePasswordUrl() + 'resetpassword/';
    }

    public getSubsKeysForAccountUrl() {
        return this.getBaseUrl() + 'subscriptionskeysforaccount/';
    }

    async changePassword(cpi: ChangePasswordInfo): Promise<OperationResult> {
        const bodyString = JSON.stringify(cpi);
        const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
        // tslint:disable-next-line: align
        return this.http
            .post<OperationResult>(this.getChangePasswordApiUrl(), bodyString, { headers })
            .pipe(
                map((res: any) => {
                    if (!res || !res.Result) {
                        this.errorMessage = res.Message + ' (Code:' + res.Code + ')';
                        return res;
                    }
                    return res;
                }),
                catchError((error: HttpErrorResponse) => {
                    console.error(`Error Code: ${error.status}\nMessage: ${error.message}`);
                    const res = new OperationResult();
                    res.Code = 662;
                    return of(res);
                })
            )
            .toPromise();
    }

    async resetpassword(accname: string): Promise<OperationResult> {
        const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
        // tslint:disable-next-line: align
        return this.http
            .post<OperationResult>(this.getResetPasswordUrl() + accname, { headers })
            .pipe(
                map((res: any) => {
                    if (!res) {
                        res = new OperationResult();
                        res.Code = 663;
                    }
                    return res;
                }),
                catchError((error: HttpErrorResponse) => {
                    console.error(`Error Code: ${error.status}.\nMessage: ${error.message}`);
                    const res = new OperationResult();
                    res.Message = `Error Code: ${error.status}.\nMessage: ${error.message}`;
                    res.Code = 661;

                    if (!this.router.routerState.snapshot.url.includes(this.getLoginPageUrl()))
                        this.router.navigate([this.getLoginPageUrl()]);
                    return of(res);
                })
            )
            .toPromise();
    }

    public logoff() {
        const logoffRequest: LogoffRequest = new LogoffRequest(this.getToken());

        return this.http
            .post<LogoffResponse>(this.getLogoutUrl(), logoffRequest)
            .pipe(
                map((logoffResponse: LogoffResponse) => {
                    if (logoffResponse.Result) {
                        console.log('AuthService: Clearing storage due to Logoff');
                        this.clearStorage();
                        this.loggedOut$.next();
                    }

                    return logoffResponse;
                })
            )
            .toPromise();
    }

    public navigateUserGateway() {
        console.log('entering navigateUserGateway..');
        let userGatewayUrl = this.getUserGatewayUrl();

        // if usergateway url exists, then redirect to it
        if (userGatewayUrl !== '') {
            console.log(`Found getUserGatewayUrl ${userGatewayUrl}`);
            document.location.href = userGatewayUrl;
            return;
        }

        // otherwise, redirect to login
        this.router.navigate([this.getLoginPageUrl()]);
    }

    public getRedirectUrlForSubscription(accountName: string, subscriptionKey: string) {
        this.getInstancesMapForUser(accountName).subscribe(
            (res) => {
                const map: Array<{ SubscriptionKey: string; Description: string; InstanceKey: string }> = res as Array<{
                    SubscriptionKey: string;
                    Description: string;
                    InstanceKey: string;
                }>;
                if (!map || map.length === 0) {
                    throw 'instanceMap is invalid';
                }
                const currentInstanceKey: string = map.filter((k) => k.SubscriptionKey === subscriptionKey).map((j) => j.InstanceKey)[0];
                this.getSnapshot(currentInstanceKey, subscriptionKey).subscribe(
                    (res) => {
                        if (!res || res === [] || res.length === 0) throw 'snapshot is empty';
                        // we have now the snapshot
                        const services: Array<{ ServiceType: string; Url: string; ProductVersion: string; Port: number }> = res[
                            'Services'
                        ] as Array<{ ServiceType: string; Url: string; ProductVersion: string; Port: number }>;

                        let redirectUrl: string = services
                            .filter((i) => i.ServiceType === 'M4FRONTEND' || i.ServiceType === 'APP_FRONTEND')
                            .map((f) => f.Url)[0];
                        console.log(`Designated redirect is ${redirectUrl}`);
                        const baseRedirectUrl = `${redirectUrl}?jwt=${this.getToken()}&subKey=${subscriptionKey}&instanceKey=${currentInstanceKey}`;

                        console.log(`Designated final redirect is ${baseRedirectUrl}`);
                        localStorage.setItem('lastLoggedRedirect', baseRedirectUrl);
                        document.location.href = 'http://' + baseRedirectUrl;
                    },
                    (err) => {
                        console.log('snapshot cannot be obtained');
                        throw 'snapshot cannot be obtained';
                        //this.router.navigate([this.getRedirectUrl()]);
                    }
                );
            },
            (err) => {
                console.log('getRedirectUrlForSubscription ia about to fail...');
                console.log(err);
                throw 'getInstancesMapForUser failed';
            }
        );
    }
    public getInstancesMapForUser(user: string) {
        return this.http.get<OperationResult>(this.getInstancesMapForAccountUrl() + user).pipe(
            map((res: any) => {
                if (!res || !res.Result) return [];
                return res.Content;
            })
        );
    }

    public async getCalendar(subscriptionKey: string) {

      return await this.http.get<OperationResult>(`${this.getCalendarUrl()}?SubscriptionKey=${subscriptionKey}`)
      .pipe(
                map((res: any) => {
                    if (!res || !res.Result) {
                        this.errorMessage = res.Message + ' (Code:' + res.Code + ')';
                        return res;
                    }
                    return res;
                }),
                catchError((error: HttpErrorResponse) => {
                    console.error(`Error Code: ${error.status}\nMessage: ${error.message}`);
                    const res = new OperationResult();
                    res.Code = 662;
                    return of(res);
                })
            )
            .toPromise();

    }


    public getSnapshot(instanceKey: string, subscriptionKey: string) {
        return this.http.get<OperationResult>(this.getSnapshotServiceUrl() + instanceKey + '?subscriptionKey=' + subscriptionKey).pipe(
            map((res: any) => {
                if (!res || !res.Result) return [];
                return res.Content;
            })
        );
    }

    public getInstancesMapForAccountUrl() {
        return this.getBaseUrl() + 'instancesMap/';
    }

     public getCalendarUrl() {
        return this.getIupUrl() + 'calendarjobs/';
    }

     public getUpdateMessage() {
      if (navigator.language === 'it' || navigator.language === 'it-IT' || navigator.language === 'it-CH')
        return this.getUpdateMessageIt();
      else
         return this.getUpdateMessageEn();
     }

    public clearStorage() {
        sessionStorage.removeItem(StorageVars.JWT);
        sessionStorage.removeItem(StorageVars.CULTURE);
        sessionStorage.removeItem(StorageVars.UI_CULTURE);
        sessionStorage.removeItem(StorageVars.ACCOUNT_ROLES);

        localStorage.removeItem(StorageVars.JWT);
        localStorage.removeItem(StorageVars.CULTURE);
        localStorage.removeItem(StorageVars.UI_CULTURE);
        localStorage.removeItem(StorageVars.ACCOUNT_ROLES);
        localStorage.removeItem(StorageVars.USER_GATEWAY_AUTOREDIRECT);
    }

    storageSubscriptionData(subscriptionKey: string, subscriptionDescription: string) {
        if (this.env.auth.sessionStorage) {
            sessionStorage.setItem(StorageVars.SUBSCRIPTION, subscriptionKey);
            sessionStorage.setItem(StorageVars.SUBSCRIPTION_DESCRIPTION, subscriptionDescription);
        } else {
            localStorage.setItem(StorageVars.SUBSCRIPTION, subscriptionKey);
            localStorage.setItem(StorageVars.SUBSCRIPTION_DESCRIPTION, subscriptionDescription);
        }
    }

    storageQueryParams(subscriptionKey: string, instanceKey: string) {
        if (this.env.auth.sessionStorage) {
            sessionStorage.setItem(StorageVars.SUBSCRIPTION, subscriptionKey);
        } else {
            localStorage.setItem(StorageVars.SUBSCRIPTION, subscriptionKey);
        }
        this.setInstanceKey(instanceKey);
    }

    private storageData(loginResponse: LoginResponse) {
        const respCulture: string =
            loginResponse.RegionalSettings === undefined || loginResponse.RegionalSettings.length === 0
                ? window.navigator.language
                : loginResponse.RegionalSettings;
        const respUiCulture: string =
            loginResponse.Language === undefined || loginResponse.Language.length === 0
                ? window.navigator.language
                : loginResponse.Language;

        if (this.env.auth.sessionStorage) {
            sessionStorage.setItem(StorageVars.JWT, loginResponse.JwtToken);
            sessionStorage.setItem(StorageVars.CULTURE, respCulture);
            sessionStorage.setItem(StorageVars.UI_CULTURE, respUiCulture);
            sessionStorage.setItem(StorageVars.ACCOUNT_ROLES, JSON.stringify(loginResponse.Roles));

            if (loginResponse.AccountName) sessionStorage.setItem(StorageVars.ACCOUNT_NAME, loginResponse.AccountName);
            if (loginResponse.SubscriptionKey) sessionStorage.setItem(StorageVars.SUBSCRIPTION, loginResponse.SubscriptionKey);
            if (loginResponse.SubscriptionDesc)
                sessionStorage.setItem(StorageVars.SUBSCRIPTION_DESCRIPTION, loginResponse.SubscriptionDesc);
        } else {
            localStorage.setItem(StorageVars.JWT, loginResponse.JwtToken);
            localStorage.setItem(StorageVars.CULTURE, respCulture);
            localStorage.setItem(StorageVars.UI_CULTURE, respUiCulture);
            localStorage.setItem(StorageVars.ACCOUNT_ROLES, JSON.stringify(loginResponse.Roles));

            if (loginResponse.AccountName) localStorage.setItem(StorageVars.ACCOUNT_NAME, loginResponse.AccountName);
            if (loginResponse.SubscriptionKey) localStorage.setItem(StorageVars.SUBSCRIPTION, loginResponse.SubscriptionKey);
            if (loginResponse.SubscriptionDesc) localStorage.setItem(StorageVars.SUBSCRIPTION_DESCRIPTION, loginResponse.SubscriptionDesc);
        }
    }

    saveCulture(culture: string, uiCulture: string) {
        if (this.env.auth.sessionStorage) {
            sessionStorage.setItem(StorageVars.CULTURE, culture);
            sessionStorage.setItem(StorageVars.UI_CULTURE, uiCulture);
        } else {
            localStorage.setItem(StorageVars.CULTURE, culture);
            localStorage.setItem(StorageVars.UI_CULTURE, uiCulture);
        }
    }

    getToken() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.JWT);
        else return localStorage.getItem(StorageVars.JWT);
    }

    getAccountName() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.ACCOUNT_NAME);
        else return localStorage.getItem(StorageVars.ACCOUNT_NAME);
    }

    getSubscription() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.SUBSCRIPTION);
        else return localStorage.getItem(StorageVars.SUBSCRIPTION);
    }

    getSubscriptionDescription() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.SUBSCRIPTION_DESCRIPTION);
        else return localStorage.getItem(StorageVars.SUBSCRIPTION_DESCRIPTION);
    }

    getCulture() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.CULTURE);
        else return localStorage.getItem(StorageVars.CULTURE);
    }

    getUICulture() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.UI_CULTURE);
        else return localStorage.getItem(StorageVars.UI_CULTURE);
    }

    getInstanceKey(): string | null {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.INSTANCEKEY);
        else return localStorage.getItem(StorageVars.INSTANCEKEY);
    }

    setInstanceKey(instanceKey: string) {
        if (this.env.auth.sessionStorage) sessionStorage.setItem(StorageVars.INSTANCEKEY, instanceKey);
        else localStorage.getItem(StorageVars.INSTANCEKEY);
    }

    getAuthServiceUrl = (): string => this.env.auth.url;
    getIupUrl = (): string => this.env.auth.iupurl;
    getRedirectUrl = (): string => this.env.auth.redirectUrl;
    getUserGatewayUrl = (): string => this.env.auth.userGatewayUrl;
    getCreateAccountUrl = (): string => this.env.auth.createAccountUrl;
    getChangePasswordUrl = (): string => this.env.auth.changePasswordUrl;
    hasSubscriptionSelection = (): boolean => this.env.auth.subscriptionSelection;
    showSignUp = (): boolean => this.env.auth.showSignUp;
    getAppId = (): string => this.env.auth.appId;
    isSessionStorage = (): boolean => this.env.auth.sessionStorage;
    getLogoURL = (): string => this.env.auth.logoURL;
    isRedirectExternal = (): boolean => this.env.auth.isRedirectExternal;
    getUpdateMessageIt = (): string => this.env.auth.updatemessageIt;
    getUpdateMessageEn = (): string => this.env.auth.updatemessageEn;
}
